<template>
  <div class="create-page">
    <app-loading :active="loading" spinner="spinner" color="#eb184d" />
    <app-header></app-header>
    <div class="animated fadeIn content">
      <h2 class="title">CREAR BOTÓN WHATSAPP</h2>
      <template v-if="!url">
        <div class="description">
          Ingresa el número y el mensaje que deseas enviar
        </div>
        <form class="full">
          <template v-if="editing">
            <label for="phone">Tu número</label>
            <input class="input-phone" type="text" name="phone" v-model="editPhone">
            <i class="fas fa-times cancel" @click="onCancel" v-if="whatsapp.phone"></i>
            <button class="btn save small green" @click="onSave">GUARDAR</button>
          </template>
          <template v-else>
            <label for="phone">Tu número</label>
            <div class="display-phone">{{whatsapp.phone}}</div>
            <i class="fas fa-edit" @click="onEdit"></i>
          </template>
          <div v-if="errors.phone" class="error">
            {{errors.phone}}
          </div>
          <label for="text">Tu mensaje</label>
          <textarea class="full" name="text" v-model="whatsapp.text"></textarea>
          <div v-if="errors.text" class="error">
            {{errors.text}}
          </div>
          <div class="finish">
            <button class="btn save full green" @click="createLink">GENERAR LIGA</button>
          </div>

        </form>
      </template>
      <template v-else>
        <div class="url">
          <a :href="url" class="link">{{url.length>60 ? `${url.slice(0,60)}...` : url}}</a>
        </div>
        <button class="btn green full copy" @click="copyToClipboard">COPIAR LIGA</button>
        <div class="btn-link" @click="onGoBack">EDITAR</div>
        <div class="btn-link" @click="onDone">GUARDAR Y SALIR</div>
        <div class="info animated fadeOut" v-if="info"><i class="fas fa-info-circle"></i> {{this.info}}</div>
      </template>
    </div>
  </div>
</template>
<script>
import ElementLoading from 'vue-element-loading';
import Header from './Header.vue';
import config from '../config';
export default {
  name: 'create',
  beforeCreate() {
    this.links = JSON.parse(localStorage.getItem('links')) || [];
    this.index = this.$route.params.id !== 'new' ? this.$route.params.id : undefined;
    if (this.index !== undefined) {
      this.editing = false;
      this.whatsapp = {
        phone: this.links[this.index].phone,
        text: this.links[this.index].text
      }
      this.editPhone = this.links[this.index].phone;
    } else {
      this.editing = true; 
      this.whatsapp = {
        phone: '',
        text: ''
      };
      this.editPhone = '';
    }

  },
  beforeRouteEnter (to, from, next) {
    const credentials = JSON.parse(localStorage.getItem('credentials'));
    if (!credentials) {
      return next('/login');
    }
    next();
  },
  data() {
    return {
      editing: this.editing,
      whatsapp: this.whatsapp,
      editPhone: this.editPhone,
      url: '',
      loading: false,
      info: '',
      errors: {}
    };
  },
  methods: {
    validPhone() {
      const phone = this.editPhone;
      let valid = true;
      if(!phone) {
        this.errors = {
          ...this.errors,
          phone: 'Debe ingresar un celular'
        };
        valid = false;
      } else if (phone.length < 10) {
        this.errors = {
          ...this.errors,
          phone: 'Ingrese un número celular de 10 digitos mínimo'
        };
        valid = false;
      } else if (phone.match(/[^0-9]/g)) {
        this.errors = {
          ...this.errors,
          phone: 'Solo introduzca números'
        };
        valid = false;
      }
      if (valid) {
        delete this.errors.phone;
      }
      return valid;
    },
    validData() {
      let valid = true;
      const validPhone = this.validPhone();
      if(!validPhone) {
        valid = false;
      } 
      if(this.editing) {
        this.errors = {
          ...this.errors,
          phone: 'Guarde el celular'
        };
        valid = false;
      }
      if (!this.whatsapp.text) {
        this.errors = {
          ...this.errors,
          text: 'Debe introducir un mensaje'
        };
        valid = false;
      } else {
        this.errors = {
          ...this.errors,
          text: undefined
        }
      }
      return valid;
    },
    onEdit() {
      this.editing = true;
      console.log('editing')
    },
    onGoBack() {
      this.url = '';
    },
    onSave(e) {
      e.preventDefault();
      const phone = this.editPhone;
      this.whatsapp.phone = phone;
      if (this.validPhone()) {
        this.errors = {};
        this.editing = false;
      }
    },
    onCancel() {
      this.errors = {};
      this.editPhone = this.whatsapp.phone;
      this.editing = false;
    },
    createLink(e) {
      e.preventDefault();
      if (this.validData()) {
        const phone = this.whatsapp.phone.slice(0,3) === '521' ? phone : `521${this.whatsapp.phone}`;
        const url = encodeURI(`${config.whatsapp.base}${phone}?text=${this.whatsapp.text}`);
        this.url = url;
      }
    },
    copyToClipboard() {
      const aux = document.createElement("input");
      aux.setAttribute("value", this.url);
      document.body.appendChild(aux);
      aux.select();
      document.execCommand("copy");
      document.body.removeChild(aux);
      this.info = 'Copiado';
      setTimeout(() => {
        this.info = '';
      }, 2000);
    },
    onDone() {
      this.loading = true;
      let links = this.links || JSON.parse(localStorage.getItem('links')) || [];
      const link = {
        id: this.index || links.length,
        ...this.whatsapp,
        date: Date.now(),
        url: this.url
      }
      link.id = link.id.toString();
      setTimeout(() => {
        if (this.index) {
          links[this.index] = link;
        } else {
          links.push(link);
        }
        links = links.filter(link => link)
        localStorage.setItem('links', JSON.stringify(links));
        this.loading = false;
        this.$router.push('/links');
      }, 2000);
    }
  },
  components: {
    'app-loading': ElementLoading,
    'app-header': Header
  }
};
</script>
<style scoped>
.content {
  margin: 0 auto;
  width: 80%;
}
.title {
  margin-top: 3rem;
  font-size: 2rem;
  font-weight: bold;
}
.description {
  display: inline-block;
}
.input-phone {
  width: 15rem;
  font-size: inherit;
  padding-right: 3rem;
}
.display-phone {
  display: inline-block;
  font-weight: bold;
}
.fas.fa-edit {
  cursor: pointer;
}
.cancel {
  position: absolute;
  transform: translate(-25px, 12px);
  cursor: pointer;
}
.green {
  background-color: #249c74;
}
.save {
  margin: 0;
  margin-left: 5px;
  padding: 1rem;
  border-left: none;
  border-top: none;
}
.save.small{
  width: 12rem;
}
.finish {
  width: 80%;
  margin: 0 auto;
  padding-top: 3rem;
}
.url {
  margin-top: 6rem;
  margin-bottom: 2.5rem;
}
.btn-link {
  margin-top: 2rem;
  text-decoration: underline;
  font-weight: bold;
  cursor: pointer;
}
a.link {
  color: #3096e0;
}
form {
  margin: 3rem 0;
  text-align: left;
  position: relative;
}
label {
  display: block;
  margin-top: 2rem;
  margin-bottom: 1rem;
}
textarea,
input {
  resize: none;
  border-radius: 3px;
  background-color: #f5f5f5;
  border: 2px solid #dadada;
  padding: 1rem;
  box-sizing: border-box;
}
textarea {
  height: 10rem;
}
@media screen and (min-width: 580px) {
  .content {
    width: 50rem;
    margin: 0 auto;
  }
}
@media screen and (max-width: 343px) {
  .save.small {
    margin: 1rem 0;
  }
}
</style>




